import * as React from 'react';
import { Theme } from '@material-ui/core/styles/createMuiTheme';
import createStyles from '@material-ui/core/styles/createStyles';
import withStyles, { WithStyles } from '@material-ui/core/styles/withStyles';
import ReactEcharts from "echarts-for-react";
import 'echarts/map/js/china';
import withRoot from '../../../withRoot';

const styles = (theme: Theme) =>
  createStyles({
    root: {
      height: '100%',
    },
  });

type State = {
  open: boolean;
};

var chinaGeoCoordMap: any = {
  '北京市': [116.4551, 40.2539],
  "山东": [117.1582, 36.8701],
  "河南": [113.4668, 34.6234],
  "江苏": [118.8062, 31.9208],
  "浙江": [119.5313, 29.8773],
};
var chinaDatas: any = [
  [{
    name: '北京市',
    value: 0
  }],
  [{
    name: '山东',
    value: 0
  }],
  [{
    name: '河南',
    value: 0
  }],
  [{
    name: '江苏',
    value: 0
  }],
  [{
    name: '浙江',
    value: 1
  }],
];
var series: any = [{
  type: 'lines',
  zlevel: 2,
  effect: {
    show: true,
    period: 4, //箭头指向速度，值越小速度越快
    trailLength: 0.02, //特效尾迹长度[0,1]值越大，尾迹越长重
    symbol: 'arrow', //箭头图标
    symbolSize: 5, //图标大小
  },
  lineStyle: {
    normal: {
      width: 1, //尾迹线条宽度
      opacity: 1, //尾迹线条透明度
      curveness: .3 //尾迹线条曲直度
    }
  },
  data: [
    [{ coord: chinaGeoCoordMap['河南'], value: 0 }, { coord: chinaGeoCoordMap['山东'] }],
    [{ coord: chinaGeoCoordMap['河南'], value: 0 }, { coord: chinaGeoCoordMap['北京市'] }],
    [{ coord: chinaGeoCoordMap['河南'], value: 0 }, { coord: chinaGeoCoordMap['江苏'] }],
    [{ coord: chinaGeoCoordMap['河南'], value: 0 }, { coord: chinaGeoCoordMap['浙江'] }],
    [{ coord: chinaGeoCoordMap['北京市'], value: 0 }, { coord: chinaGeoCoordMap['浙江'] }],
  ]
}, {
  type: 'effectScatter',
  coordinateSystem: 'geo',
  zlevel: 2,
  rippleEffect: { //涟漪特效
    period: 4, //动画时间，值越小速度越快
    brushType: 'stroke', //波纹绘制方式 stroke, fill
    scale: 4 //波纹圆环最大限制，值越大波纹越大
  },
  label: {
    normal: {
      show: true,
      position: 'right', //显示位置
      offset: [5, 0], //偏移设置
      formatter: function (params: any) { //圆环显示文字
        return params.data.name;
      },
      fontSize: 13
    },
    emphasis: {
      show: true
    }
  },
  symbol: 'circle',
  symbolSize: function (val: any) {
    return 5 + val[2] * 5; //圆环大小
  },
  itemStyle: {
    normal: {
      show: false,
      color: '#f00'
    }
  },
  data: chinaDatas.map(function (dataItem: any) {
    return {
      name: dataItem[0].name,
      value: chinaGeoCoordMap[dataItem[0].name].concat([dataItem[0].value])
    };
  }),
},
//被攻击点
{
  type: 'scatter',
  coordinateSystem: 'geo',
  zlevel: 2,
  rippleEffect: {
    period: 4,
    brushType: 'stroke',
    scale: 4
  },
  label: {
    normal: {
      show: true,
      position: 'right',
      //offset:[5, 0],
      color: '#0f0',
      formatter: '{b}',
      textStyle: {
        color: "#0f0"
      }
    },
    emphasis: {
      show: true,
      color: "#f60"
    }
  },
  symbol: 'pin',
  symbolSize: 50,
  data: [{
    value: chinaGeoCoordMap['浙江'].concat([10]),
  }],
}]

class Location extends React.Component<WithStyles<typeof styles>, State> {
  state = {
    open: false,
  };

  handleClose = () => {
    this.setState({
      open: false,
    });
  };

  handleClick = () => {
    this.setState({
      open: true,
    });
  };

  render() {
    const { classes } = this.props
    return (
      <div className={classes.root}>
        <ReactEcharts
          style={{ height: '100%' }}
          option={{
            tooltip: {
              trigger: 'item',
              backgroundColor: 'rgba(166, 200, 76, 0.82)',
              borderColor: '#FFFFCC',
              showDelay: 0,
              hideDelay: 0,
              enterable: true,
              transitionDuration: 0,
              extraCssText: 'z-index:100',
              formatter: function (params: any) {
                //根据业务自己拓展要显示的内容
                var res = "";
                var name = params.name;
                var value = params.value[params.seriesIndex + 1];
                res = "<span style='color:#fff;'>" + name + "</span><br/>数据：" + value;
                return res;
              }
            },
            backgroundColor: "#013954",
            visualMap: { //图例值控制
              min: 0,
              max: 1,
              calculable: true,
              show: false,
              color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#00eaff'],
              textStyle: {
                color: '#fff'
              }
            },
            geo: {
              map: 'china',
              zoom: 2,
              label: {
                emphasis: {
                  show: false
                }
              },
              roam: true, //是否允许缩放
              itemStyle: {
                normal: {
                  color: 'rgba(51, 69, 89, .5)', //地图背景色
                  borderColor: '#516a89', //省市边界线00fcff 516a89
                  borderWidth: 1
                },
                emphasis: {
                  color: 'rgba(37, 43, 61, .5)' //悬浮背景
                }
              }
            },
            series: series
          }}
          notMerge={true}
          lazyUpdate={true}
          theme='dark'
          onChartReady={() => {

          }}
          onEvents={{}}
          opts={{}}
        />
      </div>
    );
  }
}

export default withRoot(withStyles(styles)(Location));
